import { Component, OnInit, ViewChild } from '@angular/core';
import { Role } from '../../_models/Role';
import { RoleService } from '../../_services/role.service';
import { AlertifyService } from '../../_services/alertify.service';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-role',
  templateUrl: './role.component.html',
  styleUrls: ['./role.component.css']
})
export class RoleComponent implements OnInit {
  model: any = {};
  roles: Role[];
  roleid: any =  Number(localStorage.getItem('RoleID'));
  @ViewChild('infoModal') public infoModal: ModalDirective;
  constructor(private roleService: RoleService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadRoles();
  }
  loadRoles() {
    return this.roleService.getRoles().subscribe((roles: Role[]) => {
      this.roles = roles;
    }, error => {
      this.alertify.error(error);
    });
  }

  AddRole() {
    if (!this.model.RoleTitle) {
      this.alertify.error('Enter Role Title');
      return;
    }
    this.roleService.AddRole(this.model).subscribe((roles: Role[]) => {
      this.roles = roles;
      this.model.RoleTitle = '';
      this.alertify.success('Role Added Successful');
      this.infoModal.hide();
    }, error => {
      this.alertify.error(error);
    });
  }

}
